footer {
    padding: 0.5rem;
}

.footer {
    background-color: $color-normal;
    color: $color-white;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
}

.footer__list {
    display: flex;
    justify-content: space-around;
}

.footer__link {
    display: block;
    padding: 1rem;
    color: $color-white;
    text-decoration: none;
}